.logo {
    font-family: $fw;
    position: relative;
    max-width: 260px;
    @media (min-width:600px) {
        margin: 0 auto 90px auto;
        padding-top: 125px;
    }
    line-height: 1;
    margin: 0 auto;
    
    &-top {
        font-size: 150px;
    }
    &-bot {
        font-size: 36px;
        position: absolute;
        left: 51px;
        bottom: 7px;
    }
}
.stek {
    font-family: $fw;
    font-size: 36px;
    display: flex;
    span {
        padding: 0 10px;
        display: block;
    }
    @media (min-width:730px) {
        justify-content: center;
        align-items: center;
        flex-wrap: nowrap;
        padding: 0;
        span {
            padding: 0;
            &:not(:first-child){
                &:before {
                    content: '-';
                    display: inline-block;
                    padding: 0 10px;
                }
            }

        } 
    }
    
    justify-content: center;
    flex-wrap: wrap;
    padding-left: 15px;
    padding-right: 15px;
    
    
}
h1 {
    font-family: $fr;
    padding: 25px;
    font-weight: 100;
    text-align: center;
    font-size: 36px;
    @media (max-width:500px) {
        font-size: 23px;
    }
}