.portfolio {
    max-width: 950px;
    margin: 0 auto;
    
    display: flex;
    align-items: stretch;
    justify-content: center;
    @media (max-width:960px) {
        height: auto!important;   
    }
    &-list {
        display: flex;
        flex-direction: column;
        font-weight: 600;
        text-transform: uppercase;
        position: relative;
        @media (max-width:960px) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
        }
        a {
            line-height: 1;
            padding: 1px 0;
            @media (max-width:960px) {
                width: 30%;
                padding: 10px 1.5%;
            }
            @media (max-width:785px) {
                width: 47%;
                padding: 10px 1.5%;
            }
            @media (max-width:490px) {
                width: 97%;
                padding: 10px 1.5%;
            }
            
            
            span {
                @media (min-width:960px) {
                    opacity: 0.65;
                }
                transition: all ease 0.4s;
                position: relative;

            }
            &.active {
                span {
                    opacity: 1;
                }
            }
        }
        &_line {
            transition: ease all 0.4s;
            position: absolute;
            right: 0;
            
                left: 436px;
            
            height: 4px;
            transform: translate(0px, -2px);
            
            background: #ef3124;
            background: -moz-linear-gradient(left, #ef3124 0%, #f4851e 100%);
            background: -webkit-linear-gradient(left, #ef3124 0%,#f4851e 100%);
            background: linear-gradient(to right, #ef3124 0%,#f4851e 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ef3124', endColorstr='#f4851e',GradientType=1 );
        }
            
    }
    &-img {
        margin-left: 25px;
        &_block {
            width: 400px;
            overflow: hidden;
            position: relative;
            transition: all ease 0.4s;
            a {
                display: none;
            }
            .active + a {
                display: block;
                text-align: right;
                &:hover {
                    text-decoration: underline;
                }
            }
            img {
                width: 100%;
                min-width: 100%;
                display: none;
                &.active {
                    display: block;
                    + div {
                        display: block;
                    }
                }
                + div {
                    display: none;
                }
            }
        }
    }
}
.info {
    width: 100%;
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    @media (max-width:600px) {
        & > * {
            width: 50%;
        }
    }
    
}