@import "variables";
@import "mixins";

body {
    background-color: #161616;
    background-image: url(../images/bg.jpg);
        background-repeat: no-repeat;
        background-position: top center;
    color: #fff;
    
    font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    
    font-weight: normal;
    line-height: 1.5;
    
}
a {
    color: #fff;
    text-decoration: none;
}
header {
    font-family: $fw;
    font-size: 24px;
    
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    a {
        padding-left: 15px;
        padding-right: 15px;
    }
    nav {
        display: flex;
        width: 100%;
        justify-content: center;
    }
}
.wrapper {
    max-width: 1030px;
    margin: 0 auto;
}
footer {
    display: flex;
    justify-content: center;
    align-items: center;
    
    flex-direction: column;
    padding: 15px 0;
}
section {
    font-family: $fr;
    padding: 25px 0;
    &.first {
        
    }
}
.flex {
    display: flex;
}